@import url(https://fonts.googleapis.com/css?family=Muli:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Muli:400,700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.App {
  font-family: 'Muli', sans-serif;
  text-align: center;
}
.App-header {
  /* background-color: #282c34; */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* color: white; */
}


.navbar-logo {
  font-size: 16px;
  font-weight: bold;

}

.navbar {
  display: flex;
  justify-content: space-between;
}
.navbar-links {
  display: inline-block;
  font-size: 14px;
  color: #2c4361;
  background-color: #f2ffff;
  padding: 8px;
}

.navbar a {
  color: #2c4361;
  text-decoration: none;
  padding: 10px;
  border-radius: 8px;
}

html,
body,
section {
  min-height: 100vh;
  min-width: 100vw;
}

div {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.left-header,
.right-header {
  font-family: "Muli", sans-serif;
  font-size: 1.75rem;
  margin: 0 0 0.75rem 0;
}

.left-text,
.right-text {
  line-height: 150%;
  font-size: 1.25rem;
  line-height: 150%;
}

/* Pattern styles */
.container {
  display: flex;
  flex-flow: wrap;
  text-align: center;
}

.left-half {
  background-color: #c6e8e9;
  flex: 1 1 100%;
  padding: 1rem;
}
.right-half {
  background-color: #a9c5c6;
  padding: 1rem;
}
.image-cropper {
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
}
.profile-photo {
  margin:20px;
  width: 100px;
  height: 125px;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

@media only screen and (min-width: 500px) {
  .left-half {
    background-color: #c6e8e9;
    flex: 1 1;
    padding: 1rem;
  }
  .right-half {
    background-color: #a9c5c6;
    flex: 1 1;
    padding: 1rem;
  }
}

.case-study {
    background-color: #A9C6C7;
}
.single-study-container {
  background-color: whitesmoke;
  margin: 20px;
  padding: 20px;
  border-radius: 8px;
  border: 1px solid #050038;
}
.app-name {
  padding: 20px;
}
.app-description {
  padding: 20px;
}
.feature-app-container {
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
  margin: none;
}
.app-photo {
  width: 180px;
  height: auto;
  border-radius: 8px;
}

.cs-title {
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
    padding: 10px;
    /* background-color: #A9C6C7 */
}

